define('front/routes/careers/job', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  var CareersJobRoute;

  CareersJobRoute = Ember['default'].Route.extend({
    model: function model(arg) {
      var jobId;
      jobId = arg.jobId;
      return this.store.find("job", jobId);
    }
  });

  exports['default'] = CareersJobRoute;

});